import VueRouter from "vue-router";
const Login = () =>
    import ("../components/Login")
const Home = () =>
    import ("../components/Home")
const Welcome = () =>
    import ("../components/Welcome")
const ClassList = () =>
    import ("../components/class/ClassList")
const StuList = () =>
    import ("../components/student/StuList")
const AddStudent = () =>
    import ("../components/student/AddStudent")
const StuItem = () =>
    import ("../components/student/StuItem")
const AddClass = () =>
    import ("../components/class/AddClass")
const AddLog = () =>
    import ("../components/class/AddLog")
const ClassLog = () =>
    import ("../components/class/ClassLog")
const LogContext = () =>
    import ("../components/class/LogContext")
const router = new VueRouter({
    routes: [{
            path: '',
            redirect: '/home/welcome'
        }, {
            name: 'home',
            path: '/home',
            component: Home,
            children: [{
                    name: 'classList',
                    path: 'classList',
                    component: ClassList
                },
                {
                    name: 'welcome',
                    path: 'welcome',
                    component: Welcome
                },
                {
                    name: 'stuList',
                    path: 'stuList',
                    component: StuList
                },
                {
                    name: 'addStu',
                    path: 'addStu',
                    component: AddStudent
                },
                {
                    name: 'StuItem',
                    path: 'stuItem',
                    component: StuItem
                },
                {
                    name: 'AddClass',
                    path: 'addClass',
                    component: AddClass
                },
                {
                    name: 'AddLog',
                    path: 'addLog',
                    component: AddLog
                }
            ]
        }, {
            name: 'login',
            path: '/login',
            component: Login,
            meta: { isAuth: true }
        },
        {
            name: 'ClassLog',
            path: '/classLog',
            component: ClassLog
        },
        {
            name: 'LogContext',
            path: '/logContext/:id',
            component: LogContext
        }
    ]
})
router.beforeEach((to, from, next) => {
    window.scrollTo(0, 0)
    if (localStorage.getItem("token") == '44b4997c9a42b02e2ed3056fcdc80878' || to.meta.isAuth) {
        next()
    } else {
        next("/login")
    }
})
export default router